<template>
    <v-ons-page id="app" modifier="material">
        <v-ons-toolbar class="toolbar-material">
            <div class="center">Bienvenido</div>
        </v-ons-toolbar>

        <div class="header" background="../assets/splas_red_social.jpg">
            <v-ons-row>
             <v-ons-col >
                  <div class="card small purple">
                    <div class="card-image">
                        <img src="../assets/amigos.jpg">
                        <span class="card-title" align="center">REDCOM</span>
                    </div>
                    <div class="card-content white-text">
                     <p>La red social donde puedes obtener ayuda en tu comunidad</p>
                    </div>
                  </div>
             </v-ons-col>
            </v-ons-row>
             <form name="login" action="#">
            <v-ons-list>
                <v-ons-list-title></v-ons-list-title>
                    <v-ons-list-item>
                        <div class="input-field">
                         <v-icon prefix>account_circle</v-icon>
                         <v-text-input name="username"
                                          id="username"
                             ></v-text-input>
                            <label for="username">Username</label>
                         </div>
                    </v-ons-list-item>
                     <v-ons-list-item>
                          <div class="center">
                            <v-ons-input name="password" type="password" required="true" placeholder="Contraseña">
                        </v-ons-input>
                        </div>
                    </v-ons-list-item>
            </v-ons-list>
                 </form>
            </v-ons-row>
        </div>
        <div align="center">
						¿Aun no te has Registrado?<router-link to="/home">Registrarse</router-link>	
					</div>
    </v-ons-page>

   
</template>
<script>
export default {
  name: 'login'
}
</script>
<style scoped>
.toolbar--material{
    background-color: purple;
}
p{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-style: Roboto;
}

</style>



